import React, { useState, useEffect } from "react";
// import axios from "axios";
// import { useCookies } from "react-cookie";
import { useNavigate, Link, Navigate } from "react-router-dom";
import { useForm } from "react-hook-form";
import "./login.scss";
import { supabase } from "../supabase";

export const Login = () => {
  const nav = useNavigate();

  const [auth, setAuth] = useState(null);

  const getLogin = async () => {
    const { data } = await supabase.auth.getSession(); //

    if (data.session) {
      setAuth(data.session);
    }
  };

  useEffect(() => {
    getLogin();
  }, []);

  const {
    register,
    handleSubmit,
    reset,
    formState: { errors },
  } = useForm();

  //
  // const [mail, setMail] = useState("");
  // const [password, setPassword] = useState("");
  var mail;
  var password;

  //
  const [logError, setLogError] = useState();

  const handleMailChange = (e) => {
    mail = e.target.value;
    console.log(mail);
  };
  const handlePasswordChange = (e) => {
    password = e.target.value;
    console.log(password);
  };

  const onLogIn = async () => {
    try {
      const { data, error } = await supabase.auth.signInWithPassword({
        email: mail,
        password: password,
      });
      reset();
      if (error) throw error;
      nav("/dieter");
    } catch (error) {
      alert(error.message);
    }
  };

  if (auth) return <Navigate to="/dieter" />;

  return (
    <div>
      <main className="main">
        <h2></h2>
        <p id="error">{logError}</p>

        <form onSubmit={handleSubmit(onLogIn)} className="login-Form">
          <label></label>
          <br />
          <input
            {...register("mail", {
              required: "",
              pattern: {
                value: /\S+@\S+\.\S+/,
                message: "",
              },
            })}
            id="login-Form__email-Set"
            type="text"
            onChange={(e) => handleMailChange(e)}
            aria-label="mailLog"
          />
          <div id="login-Form__errorM">
            {errors.mail && <span>{errors.mail.message}</span>}
          </div>
          <br />
          <br />
          <label></label>
          <br />
          <input
            {...register("password", {
              required: "",
              minLength: {
                value: 8,
                message: "824",
              },
              maxLength: {
                value: 24,
                message: "824",
              },
              pattern: {
                value: /^[a-zA-Z0-9.?/-]/,
                message: "",
              },
            })}
            id="login-Form__password-Set"
            type="text"
            onChange={(e) => handlePasswordChange(e)}
            aria-label="passLog"
          />
          <div id="login-Form__errorP">
            {errors.password && <span>{errors.password.message}</span>}
          </div>
          <br />
          <br />
          <input type="submit" id="login-Form__login-Button" value="" />
        </form>

        <br />
        <Link to="/dieter/SignUp"></Link>
      </main>
    </div>
  );
};
